* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

/* NAVIGATION */
.nav-list{
	height: 10vh;
    display: flex;
    width: 50%;
	justify-content: space-around;
	align-items: center;
	margin-left: auto;
	list-style: none;
} 

.nav-links{
	text-decoration: none;
	color: #000000;
	font-size: 20px;
}

.project,
.uni{
	height: 100vh;
    position: absolute;
    width: 100%;
}


.project{
    overflow: hidden;
	background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
}

.uni{
	background-image: linear-gradient(to top, #505285 0%, #585e92 12%, #65689f 25%, #7474b0 37%, #7e7ebb 50%, #8389c7 62%, #9795d4 75%, #a2a1dc 87%, #b5aee4 100%);
}

/* PROJECTS */
.container-projects {
	width: 100%;
    height: 90vh;
    display: flex;
	justify-content: center;
    align-items: center;
}

.image-divide{
    width: 80%;
    height: 45vh;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-color: grey white;
    scrollbar-width: thin;
}

.responsive{
    margin: 18px 0 0 18px;
}

/* MEDIA */
@media screen and (max-width: 1199px){
    .container, 
    img{
		width: 100%;
		height: 100%;
    }
}
  
/* MEDIA */
@media screen and (max-width: 255px){
    .container-projects, 
    .responsive{
		width: 100%;
		height: auto;
    }
}